body {
  font-family: Cantarell, sans-serif;
  margin: 0;
  background: rgb(255, 255, 255);
}

.listTodos {
  width: 80%;
  margin: 0 auto;
  padding: 30px 20px 40px;
}

.listTodos__title {
  text-align: center;
  font-family: Fredoka One, cursive;
  font-weight: 300;
  font-size: 32px;
  margin-bottom: 30px;
  margin-top: 0;
}

.button {
  display: block;
  margin: 0 auto;
  width: 200px;
  height: 40px;
  outline: none;
  border: 1px solid black;
  font-size: 20px;
  font-family: Cantarell, sans-serif;
  font-weight: 600;
  border-radius: 10px;
  background: #00bfff;
  color: white;
  transition: 0.1s;
}

.button--delete {
  width: 80px;
  height: 20px;
  font-size: 15px;
  font-family: Cantarell, sans-serif;
  line-height: 15px;
  border: 1px solid black;
  border-radius: 5px;
  background: #ffe449;
}

.button--delete:hover {
  transform: scale(1.1, 1.1);
}

.button:hover {
  opacity: 0.9;
  color: #ffd700;
}

.button:active {
  color: red;
}

.listTodos__sort {
  display: flex;
}

.listTodos__body {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.listTodos__todo {
  width: 300px;
  margin: 0;
}

.listTodos__todo__label {
  color: #0d11d1;
}
